import {
  IconDelete,
  IconMore,
  IconPlayCircle,
  IconStop
} from "@douyinfe/semi-icons";
import {
  Descriptions,
  Divider,
  Dropdown,
  Popconfirm,
  Table,
  Toast,
  Tooltip
} from "@douyinfe/semi-ui";
import { Data } from "@douyinfe/semi-ui/lib/es/descriptions";
import {
  ExpandedRowRender,
  OnRow
} from "@douyinfe/semi-ui/lib/es/table/interface";
import { useCallback, useContext, useEffect, useMemo, useState } from "react";
import useFetch from "use-http/dist/cjs/useFetch";
import { JobDetail, Scheduler } from "../../types";
import apiconfig from "./apiconfig";
import columns from "./columns";
import GlobalContext from "./context";
import RenderValue from "./render-value";

const style = {
  boxShadow: "var(--semi-shadow-elevated)",
  backgroundColor: "var(--semi-color-bg-2)",
  borderRadius: "4px",
  padding: "10px",
  margin: "10px",
  width: "350px",
};

export default function Jobs() {
  /**
   * 
   */
  const [jobs, setJobs] = useState<Scheduler[]>([]);
  const { rate } = useContext(GlobalContext);

  /**
   * 
   */
  const { post, response, loading } = useFetch(
    apiconfig.hostAddress,
    apiconfig.options
  );

  /**
   * 
   */
  const loadJobs = async () => {
    const data = await post("/get-jobs");
    if (response.ok) setJobs((s) => data);
  };

  /**
   * 
   */
  const callAction = async (
    jobid: string,
    triggerid: string,
    action: string
  ) => {
    const data = await post(
      "/operate-trigger?jobid=" +
        jobid +
        "&triggerid=" +
        triggerid +
        "&action=" +
        action
    );

    if (response.ok) {
      Toast.success({
        content: "",
        duration: 3,
      });
    } else {
      Toast.error({
        content: "",
        duration: 3,
      });
    }
  };

  /**
   * 
   */
  const data: JobDetail[] = useMemo(() => {
    const jobDetails: JobDetail[] = [];
    if (!jobs || jobs.length === 0) return jobDetails;

    for (const scheduler of jobs) {
      let jobDetail = scheduler.jobDetail!;
      jobDetail.triggers = scheduler.triggers;
      jobDetail.refreshDate = new Date();
      jobDetails.push(jobDetail);
    }

    return jobDetails;
  }, [jobs]);

  /**
   * 
   */
  useEffect(() => {
    const timer = setInterval(() => {
      loadJobs();
    }, rate);

    return () => {
      clearInterval(timer);
    };
  }, [rate]);

  /**
   * 
   */
  const expandRowRender: ExpandedRowRender<JobDetail> = useCallback(
    (jobDetail, index) => {
      // 
      var scheduler = jobs.find((u) => u.jobDetail?.jobId === jobDetail?.jobId);

      // 
      const triggerData: Data[][] = [];
      for (const trigger of scheduler?.triggers!) {
        const triggerItem: Data[] = [];
        for (const prop in trigger) {
          triggerItem.push({
            key: prop.charAt(0).toUpperCase() + prop.slice(1),
            value: (
              <RenderValue
                prop={prop}
                value={trigger[prop]}
                trigger={trigger}
              />
            ),
            ovalue: trigger[prop],
          } as Data);
        }

        triggerData.push(triggerItem);
      }

      return (
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          {triggerData.map((expandData, index) => (
            <div
              style={style}
              key={
                (expandData[0] as any).ovalue!.toString() +
                "_" +
                (expandData[1] as any).ovalue!.toString() +
                index
              }
            >
              <div
                style={{
                  marginTop: 3,
                  marginRight: 5,
                  marginLeft: 5,
                  display: "flex",
                  justifyContent: "space-between",
                }}
              >
                {Number((expandData[6] as any).ovalue) === 3 ? (
                  <Tooltip content="">
                    <IconPlayCircle
                      style={{ color: "red", cursor: "pointer" }}
                      size="large"
                      onClick={() =>
                        callAction(
                          (expandData[1] as any).ovalue!.toString(),
                          (expandData[0] as any).ovalue!.toString(),
                          "start"
                        )
                      }
                    />
                  </Tooltip>
                ) : (
                  <span></span>
                )}
                <Dropdown
                  render={
                    <Dropdown.Menu>
                      <Dropdown.Item
                        onClick={() =>
                          callAction(
                            (expandData[1] as any).ovalue!.toString(),
                            (expandData[0] as any).ovalue!.toString(),
                            "start"
                          )
                        }
                      >
                        <IconPlayCircle size="extra-large" /> 
                      </Dropdown.Item>
                      <Dropdown.Item
                        onClick={() =>
                          callAction(
                            (expandData[1] as any).ovalue!.toString(),
                            (expandData[0] as any).ovalue!.toString(),
                            "pause"
                          )
                        }
                      >
                        <IconStop size="extra-large" /> 
                      </Dropdown.Item>
                      <Dropdown.Item>
                        <Popconfirm
                          zIndex={10000000}
                          title={
                            " [" +
                            (expandData[0] as any).ovalue!.toString() +
                            "] "
                          }
                          onConfirm={() =>
                            callAction(
                              (expandData[1] as any).ovalue!.toString(),
                              (expandData[0] as any).ovalue!.toString(),
                              "remove"
                            )
                          }
                        >
                          <IconDelete size="small" /> &nbsp;
                        </Popconfirm>
                      </Dropdown.Item>
                    </Dropdown.Menu>
                  }
                >
                  <IconMore style={{ cursor: "pointer" }} size="large" />
                </Dropdown>
              </div>

              <Divider margin="8px" />
              <Descriptions align="left" data={expandData} />
            </div>
          ))}
        </div>
      );
    },
    [jobs]
  );

  const handleRow: OnRow<JobDetail> = (jobDetail, index) => {
    // 
    if (index! % 2 === 0) {
      return {
        style: {
          background: "var(--semi-color-fill-0)",
        },
      };
    } else {
      return {};
    }
  };

  return (
    <Table
      rowKey="jobId"
      columns={columns}
      dataSource={data}
      onRow={handleRow}
      expandedRowRender={expandRowRender}
      pagination={false}
      rowExpandable={(jobDetail) =>
        !!(
          jobDetail?.jobId &&
          jobs.find((u) => u.jobDetail?.jobId === jobDetail?.jobId)?.triggers
            ?.length !== 0
        )
      }
    />
  );
}
